@import "mixin";
#concept {
  .light-wrap {
    position: relative;
    max-width: 600px;
    // width: 24vw
    width: 100%;
    margin: auto;
    height: 90vh;
    // top: 0
    // left: 50%
    // transform: translateX(-50%)
    .line {
      height: 22%;
      width: 5px;
      margin: auto;
      background: #dec176;
      transform-origin: top center; }
    .title {
      position: absolute;
      z-index: 2;
      left: 50%;
      top: 97%;
      // top: 63%
      color: #fff;
      font-weight: normal;
      font-size: 2vw;
      margin: 0;
      transform: translate(-50%, 0%);
      border-radius: 50px;
      text-align: center;
      width: 15vw;
      padding: 1.5vh 0;
      letter-spacing: 5px;
      // border: 0.5vw solid #13387b
      background-color: #b2923e;
      // background: linear-gradient(135deg, rgba(193,162,81,1) 0%,rgba(218,188,113,1) 29%,rgba(249,221,148,1) 70%,rgba(218,188,113,1) 100%)
      text-shadow: 1px 1px 2px rgba(#000, 0.6);
      .letter {
        display: inline-block; } }
    .light {
      position: absolute;
      top: 22%;
      width: 100%;
      svg {
        display: block;
        position: relative;
        max-width: 100%;
        max-height: 60vh;
        left: 50%;
        transform: translateX(-50%); }
      &.loop {
        animation: lightLoop 2s ease-in-out infinite; } } }
  .list {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    .box {
      position: absolute;
      left: 50%;
      top: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0;
      .star {
        width: 70px;
        height: 70px;
        position: relative;
        svg {
          display: block;
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          overflow: visible;
          .star-base {
            transition: fill 0.5s; }
          .star-ani {
            opacity: 0;
            animation: starAni 2s infinite ease-in-out; } }
        &.star-2 {
          svg {
            .star-ani {
              animation-delay: 0.3s; } } }
        &.star-3 {
          svg {
            .star-ani {
              animation-delay: 0.6s; } } }
        &:hover {
          svg {
            .star-base {
              fill: #ddbd6a; }
            .star-ani {
              animation: none; } } } }
      .word {
        color: #fff;
        p {
          margin: 0;
          &.tit {
            @include font(30px);
            margin: 0 0 10px 0; }
          span {
            display: inline-block;
            position: relative;
            vertical-align: top;
            perspective: 100px;
            opacity: 0;
            // > span
            //// opacity: 0
 } } }            //// transform: rotateY(90deg)
      &.b1 {
        text-align: right;
        flex-direction: row-reverse;
        transform: translate(-100%, -50%) translate(-14vw, -5vh);
        .star {
          right: 0;
          margin-left: 30px; } }
      &.b2 {
        transform: translate(14vw, 10vh);
        .star {
          left: 0;
          margin-right: 30px; } }
      &.b3 {
        text-align: right;
        flex-direction: row-reverse;
        transform: translate(-100%, -50%) translate(-10vw, 35vh);
        .star {
          right: 0;
          margin-left: 30px; } } } }
  @include min(1921px) {
    .light-wrap {
      .light {
        .title {
          @include font(30px);
          width: 250px;
          border: 10px solid #13387b; } } } }
  @include rwd(1440px) {
    .list {
      .box {
        margin-top: -5vh;
        max-width: 240px;
        .word {
          width: calc(100% - 100px); }
        .desc {
          span:nth-child(9) {
            display: none; } }
        &.b1 {
          .star {
 } }            // width: 50px
        &.b2 {
          .star {
            left: 0; } }
        &.b3 {} } } }
  @include rwd(1200px) {
    .light-wrap {
      .light {
        .title {
          @include font(22px); } } } }
  @include rwd(1024px) {
    text-align: center;
    .light-wrap {
      height: 40vh;
      .title {
        @include font(24px);
        width: 180px; }
      .light {
        svg {
          max-height: 27vh; } } }
    .list {
      width: auto;
      position: relative;
      margin-top: 15vh;
      text-align: left;
      display: inline-block;
      .box {
        position: static;
        padding: 0 !important;
        margin: 0 0 6vh 0  !important;
        transform: none !important;
        flex-direction: row !important;
        text-align: left !important;
        max-width: inherit !important;
        justify-content: flex-start;
        .star {
          width: 60px !important;
          margin: 0 20px 0 0 !important; }
        .word {
          width: auto; } } } }
  @include rwd(767px) {
    .light-wrap {
      height: 28vh;
      .title {
        @include font(20px);
        width: 140px; }
      .light {
        svg {
          max-height: 17vh; } } }
    .list {
      width: 70%;
      margin: auto;
      margin-top: 12vh;
      .box {
        .star {
          width: 50px !important;
          margin: 0 10px 0 0 !important; }
        .word {
          p {
            &.tit {
              @include font(20px); }
            &.desc {
              @include font(14px);
              color: rgba(#fff, 0.6); } } } } } } }

@keyframes starAni {
  0% {
    opacity: 1;
    transform: translate(0, 0) sacle(1); }
  50% {
    opacity: 0;
    transform: translate(38px, -48px) scale(1.5); }
  100% {
    opacity: 0;
    transform: translate(38px, -48px) scale(1.5); } }

@keyframes lightLoop {
  0% {
    opacity: 1; }
  50% {
    opacity: 0.75; }
  100% {
    opacity: 1; } }
