@import "mixin";
#project {
  .project-bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    #slider {
      &:before {
        content: "";
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(ellipse at center, rgba(20,58,127,0) 30%,rgba(11,19,46,0.8) 60%,rgba(0, 0, 0,1) 100%);
        opacity: 0;
        transition: opacity 1s; }
      &.hover {
        &:before {
          opacity: 1; } } } }
  .title-wrap {
    position: absolute;
    top: 8%;
    left: 10%;
    color: #fff;
    z-index: 2;
    .tit {
      @include font(60px);
      text-transform: uppercase;
      .letter {
        display: inline-block;
        position: relative;
        color: #ffffff;
        vertical-align: top; } }
    .desc {
      @include font(18px);
      line-height: 2; } }
  .list {
    // display: flex
    // flex-direction: column-reverse
    position: absolute;
    right: -15px;
    bottom: -5px;
    z-index: 2;
    // transform: rotate(90deg) translateY(100%)
    // transform-origin: right bottom
    &.not-hover {
      pointer-events: none; }
    button {
      display: block;
      width: 100%;
      font-weight: bold;
      text-align: right;
      outline: none;
      border: none;
      background: none;
      color: #fff;
      padding: 10px 0;
      letter-spacing: 5px;
      line-height: 1.1;
      font-size: 5.25vw;
      cursor: none;
      -webkit-text-stroke-width: 2px;
      -webkit-text-stroke-color: #fff;
      -webkit-text-fill-color: transparent;
      &.hover {
        -webkit-text-stroke-color: transparent;
        -webkit-text-fill-color: #fff; } }
    &.is-effect {
      button:not(:hover) {
        pointer-events: none; } } }

  @include rwd(1200px) {
    .title-wrap {
      .tit {
        @include font(40px);
        margin-bottom: 20px; }
      .desc {
        @include font(16px); } } }
  @include rwd(990px) {
    padding: 20% 10%;
    .title-wrap {
      position: relative;
      text-align: center;
      top: auto;
      left: auto;
      .tit {
        @include font(60px);
        letter-spacing: 10px;
        margin-bottom: 20px; }
      .desc {
        @include font(16px); } }
    .list {
      position: relative;
      z-index: 2;
      margin-top: 8vh;
      right: auto;
      bottom: auto;
      button {
        text-align: center;
        @include font(50px); } } }
  @include rwd(767px) {
    .title-wrap {
      width: 100%;
      .tit {
        font-size: 10vw;
        letter-spacing: 1vw; }
      .desc {
        color: rgba(#fff, 0.5);
        @include font(14px); } }
    .list {
      margin-top: 6vh;
      button {
        letter-spacing: 1px;
        font-size: 7vw;
        -webkit-text-stroke-width: 1px; } } } }
