*, *:before, *:after {
  box-sizing: border-box;
  user-select: none; }
html {
  font-size: 16px; }
body {
  position: relative;
  font-size: 1rem;
  font-family: 'Microsoft JhengHei', sans-serif;
  margin: 0;
  padding: 0;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  overflow-x: hidden;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -ms-overflow-style: scrollbar;
  cursor: none;
  -webkit-overflow-scrolling: touch; }

// Global
.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  .full-section {
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0;
    transform: scale(0);
    &.current {
      z-index: 2;
      opacity: 1;
      transform: scale(1); } } }
