@import "mixin";
#service {
  .bg {
    position: absolute;
    left: 55%;
    bottom: -5%;
    width: auto;
    height: 95%;
    opacity: 0.1;
    pointer-events: none;
    svg {
      display: block;
      height: 100%; } }
  .title-wrap {
    position: absolute;
    left: 50%;
    width: 50%;
    top: 26%;
    color: #fff;
    .letter {
      display: inline-block;
      color: #ffffff;
      min-width: 15px; }
    .slogan {
      letter-spacing: 12px;
      line-height: 2;
      margin-bottom: 20px;
      @include font(20px); }
    .title {
      margin: 0;
      letter-spacing: 30px;
      @include font(100px); }
    .btn {
      border: none;
      background: none;
      letter-spacing: 5px;
      color: #ddbd6a;
      font-family: 'Microsoft JhengHei', sans-serif;
      margin-top: 50px;
      display: flex;
      align-items: center;
      cursor: pointer;
      padding: 0;
      @include font(30px);
      .icon {
        width: 50px;
        height: 50px;
        position: relative;
        margin-right: 20px;
        &:before, &:after {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          border-radius: 100%;
          background: rgba(#fff, 0.15);
          display: block; }
        &:after {
          animation: zoom 3s ease-in-out infinite; }
        i {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          display: block;
          &:before, &:after {
            content: "";
            display: block;
            position: absolute;
            background: #e3c882; }
          &:before {
            width: 16px;
            height: 2px;
            margin-left: -7px; }
          &:after {
            width: 2px;
            height: 16px;
            margin-top: -7px; } } } } }

  .list {
    height: 100%;
    width: 50%;
    padding: 10%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    .box {
      color: #fff;
      padding: 40px 0;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      letter-spacing: 5px;
      @include font(30px);
      .icon {
        width: 40px;
        height: 40px;
        margin-right: 20px;
        position: relative;
        svg {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          >* {
            fill: #DDBD6A; } } } } }
  @include rwd(1440px) {
    .title-wrap {
      .slogan {
        letter-spacing: 8px; } }
    .list {
      padding: 0 0 0 13%;
      .box {
        padding: 5vh 0;
        @include font(25px); } } }
  @include rwd(1200px) {
    .title-wrap {
      position: absolute;
      left: 50%;
      width: 50%;
      top: 26%;
      color: #fff;
      .slogan {
        max-width: 360px;
        @include font(16px); }
      .title {
        @include font(60px); }
      .btn {
        @include font(26px); } } }
  @include rwd(990px) {
    .bg {
      left: 30%; }
    .title-wrap {
      position: static;
      width: 100%;
      padding: 20vh 0 10vh 0;
      text-align: center;
      .slogan {
        max-width: 500px;
        margin: auto; }
      .title {
        letter-spacing: 10px; }
      .btn {
        display: none; } }
    .list {
      width: 100%;
      display: block;
      font-size: 0;
      .box {
        width: 50%;
        margin: 0;
        display: inline-block;
        position: relative;
        padding: 0;
        padding-left: 50px;
        margin: 3vh 0;
        .icon {
          position: absolute;
          left: 0;
          top: 0; } } } }
  @include rwd(767px) {
    text-align: center;
    padding: 10%;
    .title-wrap {
      padding: 15vh 0 5vh;
      .slogan {
        letter-spacing: 2vw;
        @include font(16px); }
      .title {
        font-size: 10vw;
        letter-spacing: 2vw; } }
    .list {
      display: inline-block;
      text-align: left;
      padding: 0;
      margin: auto;
      width: auto;
      .box {
        display: block;
        width: auto;
        margin: 5vh 0;
        .icon {
          position: absolute;
          left: 0;
          top: 0; } } } }
  @include rwd(480px) {
    .bg {
      height: 80%;
      bottom: -2%; }
    .title-wrap {
      padding: 9vh 0 0;
      .slogan {
        letter-spacing: 1vw;
        font-size: 12px;
        max-width: 300px;
        margin: 0 auto 10px;
        // word-break: keep-all
        // white-space: nowrap
        .letter {
          min-width: 4px; } } }
    .list {
      .box {
        @include font(18px);
        .icon {
          width: 30px;
          height: 30px; } } } } }
@keyframes zoom {
  0% {
    transform: scale(0.1); }
  50% {
    transform: scale(2);
    opacity: 0; }
  100% {
    transform: scale(2);
    opacity: 0; } }
