@import "mixin";
// Banner
#banner {
  .bg-canvas {
    canvas {
      display: block;
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0; } }
  .text {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    text-align: center;
    padding: 0 100px;
    transform: translate(-50%, -50%) translate(0%, 10%);
    .banner-title {
      line-height: 100px;
      font-size: 6vw;
      line-height: 1.5;
      color: #ffffff;
      letter-spacing: 1px;
      margin: 0 0 30px;
      padding-left: 1px;
      max-width: 80%;
      margin: auto;
      text-align: center; }
    .banner-slogan {
      line-height: 40px;
      font-size: 24px;
      color: #ddbd6a;
      white-space: nowrap;
      word-break: keep-all;
      margin: 3% 0;
      letter-spacing: 40px;
      padding-left: 40px;
      filter: blur(5px);
      transform: scale(1.2);
      opacity: 0; }
    .scroll {
      margin-top: 40px;
      color: #ddbd6a;
      display: block;
      position: absolute;
      top: 100%;
      left: 50%;
      letter-spacing: 3px;
      font-weight: 100;
      text-align: center;
      transform: translate(-50%, 0);
      @include font(12px); }
    .banner-line {
      display: block;
      width: 1px;
      height: 100px;
      margin: auto;
      margin-bottom: 20px;
      overflow: hidden;
      position: relative;
      &:after {
        content: '';
        display: block;
        position: absolute;
        width: 1px;
        height: 100%;
        background: #ddbd6a;
        top: -100%;
        left: 0; } } }

  @include rwd(767px) {
    .text {
      padding: 0 13%;
      transform: translate(-50%, -50%) translate(0%, 10%);
      .banner-title {
        line-height: 2;
        font-size: 7.5vw;
        max-width: inherit; }
      .banner-slogan {
        padding: 0 !important;
        letter-spacing: 1vw !important;
        @include font(18px); } } } }

@keyframes gradientRun {
  from {
    gradientTransform: translate(0,0); }
  to {
    gradientTransform: translate(0,-100%); } }
