@import "mixin";
#about {
  // position: relative
  .title-wrap {
    text-align: center;
    color: #fff;
    margin-top: 3%;
    .tit {
      @include font(60px);
      margin: 0 0 20px;
      span {
        display: inline-block;
        color: #ffffff;
        min-width: 12px;
        position: relative;
        vertical-align: top; } }
    p {
      margin: 0;
      @include font(30px); } }
  .list {
    max-width: 70%;
    margin: 3% auto 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    position: relative;
    z-index: 2;
    .bg {
      position: absolute;
      width: 100%;
      left: 50%;
      top: 50%;
      z-index: -1;
      transform: translate(-50%, -50%) translate(0%, -120px);
      .line {
        position: absolute;
        white-space: nowrap;
        overflow: hidden;
        line-height: 1;
        text-align: center;
        left: 50%;
        top: 50%;
        height: 4px;
        transform: translate(-50%, -50%);
        i {
          display: block;
          position: absolute;
          line-height: 1;
          width: 4px;
          height: 4px;
          background: #fff;
          // display: inline-block
          // vertical-align: middle
          border-radius: 100%;
          top: 0;
          left: 50%;
 }          // margin: 0 3px
        &.line1 {
          width: 100%; }
        //   height: 1px
        //   border-bottom: 3px dotted #fff
        &.line2 {
          width: 4px;
          height: 280px;
          i {
            display: block;
            top: 50%;
            left: 0;
 } }            // margin: 6px 0
        //   width: 1px
        //   left: 50%
        //   top: 0
        //   margin-top: -140px
        //   margin-left: -1px
        //   border-left: 3px dotted #fff
 }        //// transform: translate(-50%, 0%) rotate(90deg)
      .dot {
        width: 12px;
        height: 12px;
        border-radius: 50%;
        display: block;
        background: #fff;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-top: -5px;
        margin-left: -6px;
        z-index: 2;
        &:after {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          background: #fff;
          opacity: 0.1;
          border-radius: 50%;
          transform: scale(6); } } }
    .box {
      width: 340px;
      .pic {
        position: relative;
        z-index: 2;
        img {
          border-radius: 100%;
          width: 100%; }
        &:before {
          content: "";
          border: 3px dotted #fff;
          border-radius: 100%;
          position: absolute;
          left: -20px;
          bottom: -10px;
          width: 100%;
          height: 100%;
          z-index: -1; } }
      .content {
        text-align: center;
        margin-top: 30px;
        color: #fff;
        .name {
          @include font(60px);
          margin-bottom: 20px; }
        ul {
          text-align: left;
          display: inline-block;
          margin: 0;
          padding: 0;
          list-style: none;
          li {
            line-height: 2; } } }
      &.right {
        .pic {
          &:before {
            left: auto;
            right: -20px; } } } } }
  @include rwd(1440px) {
    .list {
      .box {
        width: 300px; } } }
  @include rwd(1200px) {
    .title-wrap {
      .tit {
        @include font(40px); }
      p {
        @include font(20px); } }
    .list {
      max-width: 75%;
      .bg {
        transform: translate(-50%, -50%) translate(0%, -100px);
        .line {
          &.line1 {
            width: 100%; }
          &.line2 {
 } } }            // width: 200px
      .box {
        width: 35%;
        .content {
          margin-top: 10px;
          .name {
            @include font(35px); } } } } }
  @include rwd(990px) {
    .title-wrap {
      position: static;
      text-align: center;
      margin-top: 5%;
      .tit {
        @include font(60px);
        letter-spacing: 10px; } } }
  @include rwd(767px) {
    .title-wrap {
      width: 100%;
      margin-top: 25%;
      .tit {
        font-size: 10vw;
        letter-spacing: 2vw; } }
    .list {
      .bg {
        display: none; }
      .box {
        width: 45%;
        .content {
          width: 100%;
          .name {
            @include font(25px); }
          ul {
            margin: 0;
            padding: 0;
            li {
              font-size: 0.875rem; } } } } } }
  @include rwd(480px) {
    .title-wrap {
      margin-bottom: 30px;
      .tit {
        font-size: 10vw;
        margin: 0;
        letter-spacing: 1vw;
        .letter {
          min-width: 10px; } } }
    .list {
      .box {
        .pic {
          &:before {
            left: -10px;
            bottom: -6px;
            border-width: 2px; } }
        &.right {
          .pic {
            &:before {
              right: -10px; } } }
        .content {
          .name {
            margin: 25px 0 10px;
            @include font(20px); }
          ul {
            li {
              text-align: center;
              margin-bottom: 10px;
              span {
                display: block;
                opacity: 0.5;
                @include font(12px); } } } } } } } }
