$bg: #0b132e;
@mixin gradient {
  background-color: $bg;
  background: radial-gradient(ellipse at center, rgba(20,58,127,1) 0%,rgba(11,19,46,1) 60%); }

@mixin font($num) {
  font-size: ($num / 16px) + rem; }

@mixin min($point) {
  @media only screen and (min-width: $point) {
    @content; } }

@mixin rwd($point) {
  @media only screen and (max-width: $point) {
    @content; } }
