@import "mixin";
// Global
a, button {
  cursor: pointer;
  outline: none; }
.en {
  font-family: 'PlayfairDisplay', 'Microsoft JhengHei', sans-serif;
  font-weight: normal; }
.wrapper {
  background-color: $bg;
  &:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(ellipse at center, rgba(20,58,127,1) 0%,rgba(11,19,46,1) 60%) fixed;
    transition: transform 2.5s 0.5s; }
  &.scrolled {
    &:before {
      transform: scale(1); } } }
.logo {
  position: absolute;
  width: 400px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  &.finished {
    // position: fixed
    z-index: 100;
    width: 130px;
    top: calc(10% + 52px);
    left: 50%;
    // transform: translate(-50%, 0)
    transition: width 0.8s, top 1.2s, left 1.2s, transform 0.8s; }
  svg {
    display: block;
    width: 100%;
    .logo-line {
      stroke-dasharray: 164;
      stroke-dashoffset: 164; }
    .logo-text {
      opacity: 0; }
    #g1, #g2, #g3 {
      animation: gradientRun 2s infinite linear; }
    .logo-studio {
      transition: opacity 0.8s 0.4s; } }
  &.left {
    top: 80px;
    left: 5%;
    width: 80px;
    svg {
      .logo-studio {
        opacity: 0 !important;
        transition: opacity 0.8s; } } }
  @include rwd(990px) {
    &.left {
      left: 80px; } }
  @include rwd(767px) {
    width: 280px;
    &.finished {
      width: 100px;
      top: calc(10% + 40px); }
    &.left {
      top: 60px;
      left: 60px; } }
  @include rwd(480px) {
    width: 200px;
    &.left {
      width: 60px;
      left: 40px; } } }
.bg-canvas {
  // position: relative
  // &:after
  //   content: ""
  //   position: fixed
  //   display: block
  //   top: 0
  //   left: 0
  //   width: 100%
