 }    // z-index: -1
  .bg-img {
    position: fixed;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat; }
  // +rwd(990px)
  //   canvas
 }  //     opacity: 0.2
.cursor-element {
  position: absolute;
  z-index: 9999;
  pointer-events: none;
  top: 0px;
  left: 0px;
  transition: opacity 0.5s;
  .dot {
    position: absolute;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background: #ddbd6a;
    margin-left: -2px;
    margin-top: -2px;
    transition: transform 0.5s;
    pointer-events: none; }
  .border {
    position: absolute;
    display: block;
    width: 30px;
    height: 30px;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    box-shadow: 0 0 0 1px rgba(#fff, 0.4);
    pointer-events: none;
    // transition: width 0.8s, height 0.8s, top 0.8s, left 0.8s
    transition: opacity 0.5s;
    &.hide {
      opacity: 0; } }
  &.in {
    .dot {
      transform: scale(0);
      transition: left 0.25s, top 0.25s; }
    .border {
      box-shadow: 0 0 0 1px rgba(#ffeec1, 0.4); } }
  &.not-show {
    opacity: 0; } }
.cursor-click {
  i {
    display: block;
    position: fixed;
    z-index: 200;
    border-radius: 100%;
    background: rgba(255, 238, 193, 0.5);
    pointer-events: none;
    transform: translate(-50%, -50%); } }
.bg-text {
  pointer-events: none;
  font-size: 20vw;
  letter-spacing: 30px;
  opacity: 0.1;
  font-weight: normal;
  color: #4358a1;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }
.scroll {
  margin-top: 40px;
  color: #ddbd6a;
  display: block;
  position: absolute;
  bottom: 150px;
  left: 4%;
  letter-spacing: 2px;
  text-align: center;
  z-index: 99;
  opacity: 0;
  transition: opacity 1.5s;
  @include font(12px);
  &.show {
    opacity: 1; }
  @include rwd(767px) {
    left: 10%;
    bottom: 100px; }
  span {
    display: block;
    position: absolute;
    transform: rotate(-90deg);
    margin-left: -57px;
    transform-origin: right center; }
  .line {
    display: block;
    width: 1px;
    height: 100px;
    margin: auto;
    overflow: hidden;
    position: relative;
    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 1px;
      height: 100%;
      background: #ddbd6a;
      top: -100%;
      left: 0;
      animation: bannerLine 3.8s infinite ease-in-out; } } }
.control-dots {
  position: fixed;
  top: 30%;
  left: 4%;
  width: 40px;
  margin-left: -20px;
  z-index: 99;
  // transform: translateY(-60%)
  text-align: center;
  opacity: 0;
  transition: opacity 1.5s;
  &.show {
    opacity: 1; }
  @include rwd(767px) {
    top: 20%;
    left: 10%; }
  ul {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
    width: 32px;
    margin: 8px auto;
    text-align: center;
    li {
      position: relative;
      width: 32px;
      height: 32px;
      border-radius: 100%;
      margin: 5px 0;
      padding: 13px;
      &:before {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        border: 1px solid #ddbd6a;
        border-radius: 100%;
        top: 0;
        left: 0;
        opacity: 0;
        transition: opacity 1s; }
      &:after {
        content: attr(data-text);
        color: lighten(#ddbd6a, 10);
        white-space: nowrap;
        opacity: 0;
        position: absolute;
        left: 100%;
        top: 50%;
        transform: translate(5px, -50%);
        transition: opacity 0.35s;
        @include font(12px); }
      @include min(991px) {
        &:hover {
          &:after {
            opacity: 1; } } }
      &.current {
        &:before {
          opacity: 0.5; }
        // &:after
 }        //   opacity: 1
      .dot {
        display: block;
        position: relative;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background: #c8af6f; } } }
  button {
    display: block;
    position: relative;
    width: 40px;
    height: 40px;
    padding: 0;
    border: none;
    background: none;
    border-radius: 100%;
    outline: none;
    cursor: none;
    padding: 10px 6px;
    svg {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 18px;
      height: 10px;
      transform: translate(-50%, -50%); } } }

.controller {
  position: fixed;
  z-index: 100;
  width: 50px;
  left: 4%;
  // top: 50%
  bottom: -150px;
  opacity: 0;
  transition: opacity 1.5s, bottom 1.5s;
  &.show {
    bottom: 10%;
    opacity: 1; }
  .line {
    display: block;
    position: relative;
    width: 1px;
    height: 50px;
    background: #c8af6f;
    left: 50%;
    opacity: 0.5;
    transform: translateX(-50%);
    transition: opacity 0.75s; }
  .dot {
    display: block;
    position: absolute;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background: #c8af6f;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: top 0.75s; }
  button {
    display: block;
    position: relative;
    border: none;
    box-shadow: 0 0 0 1px rgba(#ffeec1, 0.4);
    border-radius: 50%;
    width: 48px;
    height: 48px;
    background: none;
    outline: none;
    cursor: pointer;
    margin: 1px;
    span {
      display: block;
      position: absolute;
      width: 18px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      pointer-events: none;
      svg {
        display: block; } } }
  &.up {
    .line {
      opacity: 1; }
    .dot {
      top: 40%; } }
  &.down {
    .line {
      opacity: 1; }
    .dot {
      top: 60%; } }
  @include rwd(767px) {
    width: 40px;
    bottom: 5%;
    .line {
      height: 30px; }
    button {
      width: 38px;
      height: 38px;
      span {
        width: 14px; } } } }
.landscape-alert {
  display: none;
  position: fixed;
  z-index: 500;
  width: 100%;
  height: 100%;
  background-color: $bg;
  top: 0;
  left: 0;
  &:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(ellipse at center, rgba(20,58,127,1) 0%,rgba(11,19,46,1) 60%) fixed; }
  span {
    display: block;
    position: absolute;
    line-height: 20px;
    font-size: 16px;
    color: #ffffff;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); } }

@media (max-height: 480px) and (orientation: landscape) {
  html {
    pointer-events: none; }
  .landscape-alert {
    display: block; } }

@keyframes bannerLine {
  0% {
    top: -100%; }
  60% {
    top: 100%; }
  100% {
    top: 100%; } }
