@import "mixin";

.contact-form {
  position: fixed;
  z-index: -1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  visibility: hidden;
  .contact-form-mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.35);
    opacity: 0;
    transition: opacity 1s; }
  .contact-form-inner {
    position: absolute;
    width: 80%;
    max-width: 640px;
    max-height: 90%;
    background: rgba(#fff, 0.95);
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.25);
    padding: 40px 80px;
    top: 50%;
    left: 50%;
    opacity: 0;
    transform: translate(-50%, -50%);
    @include rwd(767px) {
      padding: 30px 40px; }
    .close-contact-form {
      position: absolute;
      top: 30px;
      right: -30px;
      width: 100px;
      height: 40px;
      background: #ddbd6a;
      border: none;
      color: #fff;
      text-shadow: 1px 1px 3px rgba(#000, 0.25);
      text-transform: uppercase;
      opacity: 0;
      &:before {
        content: '';
        display: block;
        position: absolute;
        right: -10px;
        top: 50%;
        height: 2px;
        width: 25px;
        margin-top: -1px;
        background: #fff; }
      span {
        display: block; }
      @include rwd(480px) {
        width: 90px;
        @include font(14px); } }
    .left {
      float: left;
      width: 100%;
      // margin-right: 5vw
      // width: 30%
      .title {
        color: #000;
        @include font(60px);
        @include rwd(1300px) {
          @include font(45px); }
        @include rwd(767px) {
          @include font(30px); } }
      .text {
        line-height: 2;
        margin-top: 3vh;
        @include font(15px);
        a {
          color: #20339c;
          margin-top: 30px;
          display: block;
          .icon {
            width: 15px;
            height: 12px;
            display: inline-block;
            margin-right: 8px;
            position: relative;
            svg {
              position: absolute;
              left: 0;
              top: 0;
              path {}
              fill: #20339c; } }
          &.google-form {
            border-radius: 30px;
            display: block;
            width: 200px;
            border: none;
            height: 50px;
            line-height: 50px;
            font-size: 16px;
            color: #fff;
            text-align: center;
            letter-spacing: 2px;
            margin-top: 20px;
            background: #ddbd6a; } } } }
    .right {
      float: left;
      width: calc(100% - 30% - 5vw);
      .ps {
        max-height: 70vh;
        .ps__rail-y {
          opacity: 1!important; } } }
    @include rwd(1024px) {
      .left, .right {
        float: none;
        width: 100%; }
      .left {
        margin-bottom: 20px;
        .text {
          line-height: 2;
          margin-top: 1vh;
          @include font(15px);
          a {
            margin-top: 10px; } } }
      .right {
        height: 55%;
        .ps {
          max-height: inherit; } } }
    @include rwd(767px) {
      width: 86%;
      .close-contact-form {
        right: -20px; }
      .left {
        .text {
          @include font(12px);
          a {
            margin: 0; } } }
      .right {
        height: 50%; } }
    .field-board {
      height: 100%;
      padding-right: 20px;
      .group {
        position: relative;
        &+ .group {
          margin-top: 20px; }
        > span {
          display: block;
          position: absolute;
          line-height: 20px;
          font-size: 14px;
          color: #999;
          top: 50%;
          left: 10px;
          margin-top: -10px;
          @include rwd(767px) {
            position: static;
            margin: 0; } }
        .input-wrap {
          border-bottom: 1px solid #ddd;
          // border-radius: 8px
          padding: 10px 0 10px 110px;
          overflow: hidden;
          .ant-input {
            display: block;
            width: 100%;
            height: 38px;
            font-size: 16px;
            color: #000;
            border: none;
            background: none;
            box-shadow: none;
            border-radius: 0;
            padding: 0 10px;
            outline: none;
            -webkit-appearance: none; }
          @include rwd(767px) {
            padding: 0;
            .ant-input {
              padding: 0; } }
          textarea.ant-input {
            line-height: 20px;
            padding: 9px 10px; } }
        .select-wrap {
          border-bottom: 1px solid #ddd;
          padding: 0 0 0 110px;
          overflow: hidden;
          @include rwd(767px) {
            padding: 0; }
          .ant-select {
            display: block;
            font-size: 16px; }
          .ant-select-selection--single {
            height: 38px;
            line-height: 38px;
            color: #000;
            border: none;
            background: none;
            box-shadow: none;
            border-radius: 0; }
          .ant-select-selection__rendered {
            line-height: 38px; }
          .ant-select-arrow {}
          .ant-select-dropdown {
            border-bottom: 1px solid #ddd;
            background: rgba(0, 0, 0, 0.95);
            .ant-select-dropdown-menu-item {
              line-height: 22px;
              font-size: 16px;
              color: rgb(255, 238, 193);
              padding: 6px 10px; }
            .ant-select-dropdown-menu-item-selected,
            .ant-select-dropdown-menu-item-active,
            .ant-select-dropdown-menu-item:hover {
              background: rgba(255, 238, 193, 0.1); } } }
        .check-group {
          border-bottom: 1px solid #ddd;
          padding: 4px 0 4px 110px;
          @include rwd(767px) {
            padding: 0; }
          .ant-checkbox-wrapper {
            display: inline-block;
            vertical-align: middle;
            margin-left: 0;
            margin-right: 20px;
            padding: 4px 0;
            .ant-checkbox {
              top: auto;
              .ant-checkbox-inner {
                width: 18px;
                height: 18px;
                border: 1px solid #000 !important;
                background: transparent;
                &:after {
                  display: none; } }
              &+ span {
                display: inline-block;
                line-height: 24px;
                font-size: 16px;
                color: #000;
                padding: 0 0 0 10px;
                vertical-align: middle; }
              &.ant-checkbox-checked {
                .ant-checkbox-inner {
                  background: #000; }
                &:after {
                  display: none; } } } } } } }
    .btn-box {
      margin-top: 30px;
      button {
        border-radius: 30px;
        display: block;
        width: 200px;
        margin: auto;
        border: none;
        height: 50px;
        line-height: 50px;
        font-size: 16px;
        color: #fff;
        letter-spacing: 2px;
        background: #ddbd6a; } } } }
.contact-switch {
  display: block;
  position: fixed;
  z-index: 120;
  width: 48px;
  height: 48px;
  border: none;
  background: none;
  box-shadow: 0 0 0 2px rgba(#c8af6f, 0.6);
  border-radius: 100%;
  right: 4%;
  bottom: 10%;
  margin: 1px;
  outline: none;
  opacity: 0;
  transition: opacity 1.5s;
  @include rwd(767px) {
    bottom: 5%; }
  &.show {
    opacity: 1; }
  &:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    background: rgba(#fff, 0.15);
    display: block;
    pointer-events: none;
    animation: zoom 3s ease-in-out infinite; }
  span {
    display: block;
    svg {
      display: block;
      width: 20px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      path {}
      fill: #c8af6f; } } }
